import {goWindow} from "@/api/navigate.js"
import {callPhone} from "@/api/callphone.js"
import * as MemberApi from "@/api/member/member.js"
export default{
	data(){
		return{
			bailList:[],
			freeze_money:0,
			page_index:1,
			page_count:0,
		}
	},
	onLoad(option) {
		this.freeze_money = parseFloat(option.num);
		
	},
	onShow() {
		this.getBaillist()
	},
	// 上拉加载
	onReachBottom(){
		if (this.page_index < this.page_count) {
			this.page_index++;
			getBaillist(this.page_index)
		}else{
			return
		}
		
	},
	filters:{
		changeTime(time){
			var date = new Date(parseInt(time) * 1000);
			let m = date.getMonth()+1;
			m = m < 10 ? ('0' + m) : m;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let newDate = m+ "月" + d + "日";
			return newDate
		}
	},
	methods:{
		getBaillist(page_index = 1){
			MemberApi.getbaillist({
				page_index:page_index,
				page_size:10,
				type:1,
				status:2
			}).then((res)=>{
				let data = res.data.data;
				if(page_index > 1){
					data = this.bailList.concat(data)
				}
				this.bailList = data;
				this.page_count = res.data.page_count;
			})
		},
		handleText(){
			callPhone().then((res)=>{
				try{
					uni.makePhoneCall({
					    phoneNumber: res.data.data.value.tel_service_addr
					});
				}catch(err){
					console.log(err)
				}
				
			})
		},
		// 页面跳转
		handleDetail(go_status){
			if(go_status == 2){
				goWindow("/other_pages/member/offer_records/index")
			}else if(go_status == 1){
				goWindow("/other_pages/member/offer_records/index")
			}
		}
	}
}
